.service {
	display: flex;
	flex-wrap: wrap;

	&__price {
		width: 77px;
		margin-left: auto;
		flex-shrink: 0;
		padding-left: 10px;
	}

	h3 {
		font-size: 20px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.2;
		letter-spacing: normal;
		text-align: left;
		color: #333333;
		width: 100%;
		margin-top: 30px;
	}

	.shipping-payment__number {
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.57;
		letter-spacing: normal;
		text-align: left;
		color: #262626;
	}

	strong {
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		font-weight: 700;
		line-height: 22px;
	}

	p {
		width: 100%;
		color: #333333;
		font-family: Arial, sans-serif;
		font-size: 14px;
		line-height: 22px;
		text-align: left;
		z-index: 99;
		margin-top: 1rem;

		a {
			color: #394791 !important;
		}

		span {
			width: 100%;
			padding: 5px 0;
			font-family: Arial, sans-serif;
			font-weight: 700;
		}

		strong {
			display: block;
			margin-bottom: 1rem;
		}
	}



	.table {
		width: 100%;

		&_tr {
			&:nth-child(odd) {
				// background-color: #f8f8f8;
			}

			&:first-of-type {}
		}

		&_td {
			color: #333333 !important;
			font-family: Arial !important;
			font-size: 14px !important;
			font-weight: 400 !important;

			&:first-child {
				width: unset;
				flex-grow: 1;
			}

			&:last-child {
				text-align: right;
			}



		}

	}



	@media screen and (max-width: 768px) {

		p {
			font-size: 14px;
			line-height: 22px;
		}

		.shipping-payment__number {
			color: #333333;

			font-size: 18px;
			font-weight: 500;
			line-height: 24px;
			text-transform: uppercase;
		}
	}

	@media screen and (max-width: 480px) {


		tbody {
			.table {
				width: 100%;

				&_tr:nth-child(odd) {}

				&_td {
					width: unset !important;
					flex-shrink: 1;

					&:first-child {}

					&:last-child {
						flex-basis: 90px;
						flex-shrink: 0;
					}
				}
			}
		}



	}

}
