.content-top {
	// background: url("../images/catalog.jpg") no-repeat;
	background-size: auto;
	max-width: 1436px;
	margin: 0 auto;
	min-height: 362px;
	display: flex;
	background-position-x: right;
	background-repeat: no-repeat;
	background-size: cover;

	br {
		display: none;
	}

	&__inner {
		width: 100%;
		// height: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: baseline;
		align-content: baseline;
	}

	&__tags {
		.content-top__aside {
			max-width: 463px;
			padding: 44px 41px 40px 33px;
		}

		.content-top__title_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 30px;
			font-weight: 400;
			line-height: 37px;
			text-transform: uppercase;
			margin-bottom: 9px;
			display: flex;
			flex-direction: column;
		}

		.content-top__title_bottom,
		span {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 16px;
			font-weight: 400;
		}
	}

	.container {
		padding: 0;
		display: flex;
		flex-direction: column;
		height: 100%;
	}

	&__aside {
		border-radius: 3px;
		background-color: $base;
		padding: 32px;
		max-width: 600px;
		margin-bottom: 32px;
	}

	&__title {
		width: 100%;

		&_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 32px;
			font-weight: 400;
			line-height: 1.1;
			text-transform: uppercase;
			margin-bottom: 18px;

			span {
				color: #ffffff;
				font-family: "Roboto Condensed";
				font-size: 16px;
				font-weight: 400;
			}
		}

		&_bottom {
			// margin-bottom: 91px;
			// width: 470px;
			color: #ffffff;
			font-family: Arial, sans-serif;
			font-size: 13px;
			line-height: 22px;

			p~p {
				display: none;
			}
		}
	}

	.breadcrumbs {
		width: fit-content;
		font-size: 12px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 2;
		letter-spacing: normal;
		color: #ffffff;
		margin-bottom: 10px;
		margin-top: 25px;
		padding: 0 14px;
		background: rgba(0, 0, 0, 0.21);

		div {
			align-items: center;
		}

		a {
			color: #ffffff;
		}

		&__link {
			color: white;

			a,
			span {
				font-size: 12px;
				font-family: Arial,
					sans-serif;
			}
		}
	}

	@media screen and (max-width: 767px) {
		height: auto;
		background-position-x: 100% !important;
		// background: url("../images/catalog-mobile.png") 100% 100% no-repeat;
		background-size: cover !important;
		min-height: unset;

		br {
			display: unset;
		}

		.container {
			height: 100%;
			display: flex;
			flex-direction: column;
			// justify-content: center;
		}

		.page__title.content-top__title_bottom {
			display: none;
		}

		.navchain {
			font-family: Arial, sans-serif;
			font-size: 12px;
			line-height: 24px;
			margin-top: 14px;
			text-align: center;
		}

		&__aside {
			background: unset;
			width: 100%;
			padding: 0;
			max-width: unset !important;
		}

		&__title_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 30px;
			font-weight: 400;
			line-height: 39px;
			text-transform: uppercase;
			// word-spacing: 60rem;
			text-align: center;
			width: 100%;
			background: none;
		}

		&__title_bottom {
			display: block;
		}

		&__inner {
			background: #00000030;
		}

		.breadcrumbs {
			margin-top: 30px;
			text-align: center;
			padding: 0px 16px;
			align-self: center;

			div[itemtype="http://schema.org/BreadcrumbList"] {
				justify-content: center;
			}
		}

		&__tags {
			.content-top__aside {
				padding: 0;
			}

			.breadcrumbs {
				// padding: 0;
				margin-top: 44px;
			}

			.content-top__title_top {
				color: #ffffff;
				font-family: "Roboto Condensed";
				font-size: 30px;
				font-weight: 400;
				line-height: 39px;
				text-transform: uppercase;
				margin-bottom: 14px;
			}

			.content-top__title_bottom {
				display: flex !important;
				justify-content: center;
			}
		}
	}

	@media screen and (max-width: 480px) {
		&.new-banner {
			background: #333 !important;
			min-height: unset;

			.content-top {
				&__switch {
					display: none;
				}

				&__inner {
					background: none;
					padding-top: 18px;
				}

				&__title_top {
					margin-bottom: 15px;
				}

				&__switch {
					color: #ffffff;
					font-family: "Roboto Condensed";
					font-size: 14px;
					font-weight: 400;
					line-height: 24px;
					text-transform: unset;
					border-bottom: 1px dashed #ffffff;
					width: max-content;
				}
			}

			p {
				margin-bottom: 12px;

				&:not(:first-of-type) {
					display: none;

					+.content-top__switch {
						display: block;
					}
				}
			}

			.page__title.content-top__title_bottom {
				display: block;

				&.active {
					p {
						&:not(:first-of-type) {
							display: block;
						}
					}
				}
			}

			.breadcrumbs {
				// margin-top: 0;
				background: unset;

				&__link {
					color: #ffffff;
				}
			}
		}
	}
}
